* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.footer {
  background-color: #181818; /* Colore scuro per il footer */
  color: #f1f1f1; /* Testo chiaro per un buon contrasto */
  padding: 60px 0; /* Aumento del padding sull'asse Y */
  width: 100%;
  margin: 0; /* Elimina eventuali margini che causano spazio bianco */
  box-shadow: 0 -2px 6px rgba(0, 0, 0, 0.7); /* Ombra per separare il footer dalla pagina */
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer p {
  margin: 0;
  font-size: 16px; /* Aumentato leggermente il font */
  margin-bottom: 30px;
  color: #b3b3b3;
}

.footer-links,
.footer-social {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.footer-links a {
  color: #f1f1f1;
  text-decoration: none;
  margin: 0 20px;
  font-size: 18px; /* Font leggermente più grande */
  transition: color 0.3s ease, transform 0.3s ease;
}

.footer-links a:hover {
  color: #e50914;
  transform: scale(1.1);
}

.footer-social a {
  color: #f1f1f1;
  margin: 0 20px;
  transition: color 0.3s ease, transform 0.3s ease;
}

.footer-social a:hover {
  color: #e50914;
  transform: scale(1.2);
}

/* Miglioramento della parte non responsive */
@media screen and (min-width: 769px) {
  .footer-content {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 40px;
  }

  .footer-links,
  .footer-social {
    margin: 0;
  }

  .footer p {
    margin-left: 20px;
    order: 2;
  }

  .footer-links {
    order: 1;
    flex: 1;
    display: flex;
    justify-content: flex-start;
  }

  .footer-social {
    order: 3;
    flex: 1;
    display: flex;
    justify-content: flex-end;
  }

  .footer-social a {
    margin-left: 20px;
    margin-right: 20px;
  }
}

/* Responsive design per dispositivi più piccoli */
@media screen and (max-width: 768px) {
  .footer {
    padding: 30px 0;
  }
  
  .footer-links a,
  .footer-social a {
    font-size: 16px;
    margin: 0 15px;
  }
}

/* Responsive design per dispositivi molto piccoli (fino a 400px) */
@media screen and (max-width: 400px) {
  .footer {
    padding: 20px 0;
  }
  
  .footer p {
    font-size: 14px;
  }

  .footer-links a,
  .footer-social a {
    font-size: 14px;
    margin: 0 10px;
  }

  .footer-social a {
    margin: 0 10px;
  }
}
